<style lang="less" scoped>
.header {
    display: flex;
    height: 90px;
    background-color: #fff;
    width: 100%;
    border-bottom: 1px solid #d2d2d2 !important;
    div {
      flex: 2;
      padding-left: 1.25rem;
      height: 90px;
      vertical-align: middle;
      img {
        width: 1.25rem;
        height: 1.875rem;
        vertical-align: middle;
      }
    }
    span {
      flex: 8;
      text-indent: 3em;
      vertical-align: middle;
      line-height: 1.875rem;
    }
  } // 头部
  .content{
    width: 100%;
    background-color: #fff;
    height: auto;
    &>div{
      position: relative;
      background-color: #fff;
      &>span{
        display: inline-block;
        width: 100%;
        height: 88px;
        line-height: 88px;
        text-align: center;
        font-size: 30px;
      }
      &>p{
        position: absolute;
        top: 80px;
        z-index: 2;
        width: 100%;
        &>span{
          display: block;
          width: 100%;
          text-align: center;
          font-size: 30px;
          height: 70px;
          line-height: 70px;
          background-color: #fff;
        }
      }
    }
    .content_item{
      height: 228px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      &>div{
        position: relative;
        flex: 1;
        height: 168px;
        &:nth-child(1){
          width: 300px;
        }
        &:nth-child(1){
          background-position: 0 0;
          background-repeat: no-repeat;
          background-size: 100% 100%;
          width: 300px;
          margin-left: 30px;
          border-radius: 20px;
        }
        &:nth-child(2){
          &>p{
            margin: 0;
            padding: 0;
            padding-left: 30px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            width: 300px;
            color: #464646;
          }
          &>p:nth-child(1){
            font-size: 34px;
            margin-top: 20px;
            color: #222;
          }
          &>p:nth-child(2){
            font-size: 28px;
            margin: 10px 0;
          }
          &>p:nth-child(3){
            font-size: 28px;
            &>span{
              color: #3eb140;
            }
          }
        }
        &>img{
          position: absolute;
          bottom: -8px;
          left: 50%;
          transform: translateX(-50%);
          width: 130px;
          height: 30px;
        }
      }
    }
  }
</style>

<template>
	<div class="container">
		<headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="content">
      <div>
        <span @click="showAllSchool">{{categoryName}}<img src="../../assets/img/xiala_icon_tizy.png" alt="选择"></span>
        <p :style="{'display': displaySchoolStatus}">
          <span @click="changeSchool(school, index)" :style="{color: index === currentIndex ? '#3eb140' : '#000'}" v-for="(school, index) in displaySchoolList" :key="index">{{school.categoryName}}</span>
        </p>
      </div>
      <div class="content_item" @click="selectIssue(item)" v-for="(item, index) in courses" :key="index">
        <div :style="{'background-image': 'url('+ (item.Course.coverImage ? item.Course.coverImage : defaultShowImg) +')'}">
          <img v-if="item.Course.categoryName === '美术学院'" src="../../assets/img/biaoqian_msxy_tjzy.png" alt="分类">
          <img v-if="item.Course.categoryName === '设计学院'" src="../../assets/img/biaoqian_sjxy_tjzy.png" alt="分类">
        </div>
        <div>
          <p>{{item.Course.title}}</p>
          <p>累计讲师布置: {{item.taskSize}}份</p>
          <p>我已上交: <span>{{item.jobSize}}份</span></p>
        </div>
      </div>
    </div>
	</div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
	export default {
    components: {
      headerNavBack,
    },
		data () {
			return {
        courses:[],
        searchBarFixed: null,
        centerTitle:'提交作业',
        title: '加载中',
        show_topic: 0,
        falgNoData: false,
        page: 1,
        pagination: true,
        topic_title: '未登录',
        pageSize: 10,
        categoryName: '全部学院',
        displaySchoolList: [
          {
            categiryId: '',
            categoryName: '全部学院'
          },
          {
            categiryId: 'j5m48deg',
            categoryName: '美术学院'
          },
          {
            categiryId: 'j5m484vz',
            categoryName: '设计学院'
          }
        ],
        displaySchoolStatus: 'none',
        currentIndex: 0,
        defaultShowImg: require('../../../static/images/course-large.png'),
        categorieId: ''
			}
		},
		methods: {
      showAllSchool(){
        if(this.displaySchoolStatus === 'none'){
          this.displaySchoolStatus = 'block'
        }else{
          this.displaySchoolStatus = 'none'
        }
      },
      changeSchool(school, index){
        this.currentIndex = index
        this.displaySchoolStatus = 'none'
        this.categorieId = school.categiryId
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        this.categoryName = school.categoryName
        this.page = 1
        this.pagination = true
        this.axios.get('/api/getCourseStudentById',{
          params: {
            categorieId: this.categorieId,
            page: this.page,
            pageSize: this.pageSize
          }
        }).then((res) => {
          this.courses = res.data.result
          this.$Indicator.close()
        }).catch((err) => {
            console.log(err)
        })
      },
			back(){
				this.$router.back(-1);
      },
      selectIssue(item){
        this.$router.push({
          name: 'selectCourseItem',
          params:{
            courseId:item.Course.courseId,
            courseName: item.Course.title
          }
        });
      },
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1;
          this.axios.get('/api/getCourseStudentById', {
            params: {
              categorieId: this.categorieId,
              page: this.page,
              pageSize: this.pageSize
            }
          }).then((res) => {
            if (this.pagination) {
              if (res.data.result.length > 0) {
                this.courses = this.courses.concat(res.data.result)
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有数据啦~',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
              }
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      },
    },
		created () {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
			this.axios.get('/api/getCourseStudentById',{
        params: {
          categorieId: this.categorieId,
          page: this.page,
          pageSize: this.pageSize
        }
      }).then(res => {
        this.courses = res.data.result
        this.$Indicator.close()
      }).catch(err => {
          console.log(err)
      })
    },
    mounted () {
      window.onscroll = () => {
        this.dropDown()
      }
    }
	}
</script>


